import React, { Component } from 'react'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import jwtDecode from 'jwt-decode';

//Themes
import { ThemeProvider as MuiThemeProvider } from '@material-ui/core/styles';
import createMuiTheme from '@material-ui/core/styles/createMuiTheme';

//Redux
import { Provider } from 'react-redux';
import store from './src/redux/store';
import { SET_AUTHENTICATED } from './src/redux/types';
import { logoutUser, getUserData } from './src/redux/actions/userActions';

//Components
import NavBar from './src/pages/components/layout/Navbar';
import themeObject from './src/util/theme';
import AuthRoute from './src/util/AuthRoute';

//CSS
import './App.css';
//Pages
import home from './src/pages/containers/home';
import login from './src/pages/containers/login';
import signup from './src/pages/containers/signup';
import user from './src/pages/containers/user';
import axios from 'axios';

const theme = createMuiTheme(themeObject);

const token = localStorage.FBIdToken;

if (token) {
  const decodedToken = jwtDecode(token);
  if (decodedToken.exp * 1000 < Date.now()) {//if token is expires
    store.dispatch(logoutUser());
    window.location.href = '/login';
 
  } else {
    store.dispatch({ type: SET_AUTHENTICATED });
    axios.defaults.headers.common['Authorization'] = token;
    store.dispatch(getUserData());
  }
}

class App extends Component {
    
    render() {
        return (
            <MuiThemeProvider theme={theme}>
            <Provider store={store}>
                    <Router>
                        <NavBar />
                        <div className='container'>
                            <Switch>
                                <Route exact path="/" component={home} />
                                <AuthRoute exact path="/login" component={login} /> 
                                <AuthRoute exact path="/signup" component={signup}  />
                                <Route exact path="/users/:handle" component={user}  />
                                <Route exact path="/users/:handle/scream/:screamId" component={user}  />
                            </Switch>
                        </div>
                    </Router>
                </Provider>
            </MuiThemeProvider>
        )
    }
}

export default App;



